html {
  height: 100%;
  box-sizing: border-box;
}

body {
  /* font-family: Arial, sans-serif; */
  margin: 0;
  position: relative;
  min-height: 100%;
  padding-bottom: 15rem;
  box-sizing: inherit;
}

.container {
  background-color: black;
}

header,
.footer-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 80%;
  margin: auto;
  padding: 2rem;
}

.header-logo img {
  /* width: 100px; */
}

nav ul {
  list-style-type: none;
  display: flex;
  justify-content: space-around;
  padding: 0;
}

nav ul li a {
  text-decoration: none;
  color: white;
  padding: 10px;
}

nav ul li a:hover {
  background-color: white;
  color: black;
}

main {
  margin-top: 4rem;
}

footer {
  background-color: black;
  /* margin-top: 4rem; */
  text-align: center;
  position: absolute;
  right: 0;
  left: 0;
  bottom: 0;
}

.footer-logo img {
  /* width: 50px; */
}

.footer-social {
  color: white;
}

/* .social-icons a {
  margin: 0 10px;
  color: white;
} */

.social-icons i {
  font-size: 2rem;
  margin: 0 1rem;
  cursor: pointer;
  color: white;
}

.footer-bottom p {
  color: white;
  margin: 0;
}

button {
  background-color: #F6EEE3;
  border: 1px solid black;
  padding: 10px;
  cursor: pointer;
}


/* Home Page*/
.page-container {
  width: 80%;
  margin: auto;
}

.heroPage {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #F6EEE3;
  padding: 2rem;
}

.heroPage .profileImg,
.heroPage .myName {
  flex: 1;
  display: block;
  margin: auto;
  text-align: center;
}

.profileImg img {
  border-radius: 15%;
}

.myName h1 {
  font-size: 40px;
  margin: 0;
}

.myName p {
  font-size: 18px;
}

.resumeDownload {
  display: flex;
  gap: 1rem;
  align-items: center;
  justify-content: center;
}

.resumeDownload button {
  background-color: black;
  color: #F6EEE3;
}

.resumeDownload button:hover {
  background-color: #F6EEE3;
  color: black;
}

.homeAboutMe {
  background-color: #F6EEE3;
  height: 400px;
  padding: 2rem;
  margin-top: 4rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.homeAboutMe p {
  font-size: 18px;
  width: 80%;
  margin: 0;
  line-height: 1.9rem
}

.homeProject {
  background-color: #F6EEE3;
  margin-top: 4rem;
  padding: 2rem;
}

.homeProject h2 {
  font-size: 35px;
  margin: 0;
}

.projectSection {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 1rem;
  /* margin-bottom: 8rem; */
  margin-top: 2rem;
}

.card {
  border: 1px solid black;
  display: flex;
  flex-direction: column;
  cursor: pointer;
  transition: transform 0.5s ease;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  background-color: white;
  border-radius: 10px;
}

.card img {
  flex: 3;
  width: 100%;
  object-fit: cover;
  border-radius: 10px 10px 0 0;
}

.card p {
  margin: 1rem 1rem;
  /* flex:1 */
}

.card:hover {
  transform: scale(1.02);
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
}

/* About me Page*/
.aboutme-page {
  background-color: #F6EEE3;
  padding: 3rem 0;
  width: 80%;
  margin: auto;
}

.aboutme {
  width: 80%;
  margin: auto;
}

.aboutme h2 {
  font-size: 35px;
  margin-top: 1rem;
  margin-bottom: 0;
}

.aboutme h3 {
  margin-bottom: 0;
}

.aboutme p {
  margin-bottom: 0;
  font-size: 18px;
}

.aboutme ul li {
  font-size: 18px;
}

/* Contact Page */
.contact h2 {
  font-size: 35px;
  text-align: center;
}

.contact p {
  margin: 0;
  text-align: center;
  font-size: 25px;
}

.contact-address {
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-align: center;
  margin-top: 4rem;
}

.contact-address section {
  background-color: #F6EEE3;
  width: 30%;
  height: 200px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  cursor: pointer;
  border-bottom: 2px solid black;
}

.contact-address section:hover {
  border-bottom: 2px solid red;
}

.contact-address section i {
  font-size: 2rem;
}

.contact-message {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 8rem;
  width: 100%;
  /* Ensure it takes up the full width */
}

.contact-message h3 {
  font-size: 2rem;
  width: 500px;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  border: 1px solid #ccc;
  padding: 10px;
}

.contact-message form {
  display: flex;
  flex-direction: column;
  width: 500px;
  border: 1px solid #ccc;
  padding: 10px;
}

.contact-message form input,
.contact-message form textarea {
  margin-bottom: 10px;
  padding: 10px;
  width: 100%;
  box-sizing: border-box;
}

.contact-message form input[type="email"],
.contact-message form input[type="text"],
.contact-message form textarea {
  height: 40px;
  margin-bottom: 15px;
}

.contact-message form textarea {
  height: 100px;
  resize: vertical;
  /* Allow user to resize textarea vertically */
}

.contact-message form button {
  padding: 10px;
  width: 100%;
  background-color: black;
  color: white;
  border: none;
  cursor: pointer;
  font-size: 1rem;
}

.contact-message form button:hover {
  background-color: rgb(157, 153, 153);
}


