  transform: scale(1.02);
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
}

/* SingleFile Page */
.singlefile-container {
  width: 80%;
  margin: auto;
}
.singlefile-page{
  text-align: center;
  width: 80%;
  margin: auto;
  background-color: #F6EEE3;
  padding: 2rem;
}

.singlefile-page h2{
  font-size: 35px;
}

.singlefile-text{
  width: 55%;
  margin: 3rem auto 0;
}

.singlefile-text p{
  font-size: 18px;
  text-align: center;
  
}
@media screen and (max-width:1200px) {
  header {
    flex-direction: column;
    padding-bottom: 0;
  }

  .footer-content {
    padding-bottom: 0;
  }

  .projectSection {
    grid-template-columns: 1fr 1fr;
  }
}


@media screen and (max-width:480px) {
  .heroPage {
    flex-direction: column;
  }

  .profileImg img {
    display: flex;
    margin: auto;
    width: 100%;
    /* Adjust the image width to 90% of its container */
    height: auto;
  }

  .myName h1 {
    font-size: 30px;
    margin-top: 2rem;
  }

  .myName p {
    margin-top: 0.5rem;
    margin-bottom: 0;
    /* font-size: 18px; */
  }

  .resumeDownload {
    margin-top: 0.5rem;
  }

  .homeAboutMe {
    height: 100%;
    padding: 2rem 0;
  }

  .aboutme-page {
    width: 100%;
  }

  .projectSection {
    grid-template-columns: 1fr;
  }

  main {
    margin-bottom: 5rem;
  }

  .footer-content {
    flex-direction: column;
  }

  .footer-bottom {
    margin-top: 2rem;
  }


  /* ContactP Page */
  .contact-address {
    flex-direction: column;
    gap: 4rem;
  }

  .contact-address section {
    width: 100%;
  }

  .contact-message {
    flex-direction: column;
    margin: 0;
  }

  .contact-message form {
    width: 80vw;
  }

  .contact-message h3 {
    padding: 0;
    width: 100vw;
  }

  .singlefile-page{
    padding: 1rem;
    width: 90%;
  }
  .singlefile-page img{
    width: 100%;
  }
  .singlefile-text{
    width: 100%;
    margin: 0;
  }

  .singlefile-page p{
    text-align: start;
  }
}